<div class="dye-select-wrap"><h2 class="title">色カテゴリ</h2><div class="color-category-wrap"><label v-for="selectCategory in selectCategoryArr"><input type="radio" v-model="selectCategoryColor" :value="selectCategory" name="colorCat"><div class="cursor"></div><span :class="selectCategory"></span></label></div><h2 class="title">カララント選択</h2><div class="wrap" v-if="selectCategoryColor === 'white'"><label v-for="selectDye in whiteArr"><input type="radio" v-model="selectDyes" :value="selectDye" name="color"><span :style="selectDye"></span>{{selectDye.color}}</label></div><div class="wrap" v-if="selectCategoryColor === 'red'"><label v-for="selectDye in redArr"><input type="radio" v-model="selectDyes" :value="selectDye" name="color"><span :style="selectDye"></span>{{selectDye.color}}</label></div><div class="wrap" v-if="selectCategoryColor === 'brown'"><label v-for="selectDye in brownArr"><input type="radio" v-model="selectDyes" :value="selectDye" name="color"><span :style="selectDye"></span>{{selectDye.color}}</label></div><div class="wrap" v-if="selectCategoryColor === 'yellow'"><label v-for="selectDye in yellowArr"><input type="radio" v-model="selectDyes" :value="selectDye" name="color"><span :style="selectDye"></span>{{selectDye.color}}</label></div><div class="wrap" v-if="selectCategoryColor === 'green'"><label v-for="selectDye in greenArr"><input type="radio" v-model="selectDyes" :value="selectDye" name="color"><span :style="selectDye"></span>{{selectDye.color}}</label></div><div class="wrap" v-if="selectCategoryColor === 'blue'"><label v-for="selectDye in blueArr"><input type="radio" v-model="selectDyes" :value="selectDye" name="color"><span :style="selectDye"></span>{{selectDye.color}}</label></div><div class="wrap" v-if="selectCategoryColor === 'purple'"><label v-for="selectDye in purpleArr"><input type="radio" v-model="selectDyes" :value="selectDye" name="color"><span :style="selectDye"></span>{{selectDye.color}}</label></div><div class="wrap" v-if="selectCategoryColor === 'extra'"><label v-for="selectDye in extraArr"><input type="radio" v-model="selectDyes" :value="selectDye" name="color"><span :style="selectDye"></span>{{selectDye.color}}</label><p class="notice">※上記以外のカララントは全てNPCからは入手不可</p></div></div>