<div id="shop-list"><div class="shop-list-wrap" v-if="selectDyes !== ''"><h3 class="title">{{selectDyes.color}}の入手先一覧</h3><ul><li v-for="shopId in shopIds"><dl><dt>{{shopId.name}}</dt><dd v-if="shopId.place !== ''"><p>{{shopId.place}}</p><span v-if="shopId.place !== ''" @click="mapOpen" :data-mapId="shopId.id">MAP</span></dd></dl></li></ul><p class="notice">※クエスト報酬、IDでの宝箱ドロップは除外しています</p><div class="page-top-button"><button @click="gotoTop">ページ最上部へ戻る</button></div></div><div class="map-modal" v-if="mapModal === 'open'"><div class="map-modal-inner"><img :src="require(`@/assets/images/map/${mapId}.jpg`)"><div class="button"><button @click="mapClose">マップを閉じる</button></div></div></div></div>